<template lang="html">
  <div class="row">
    <div class="col-lg-12">
      <h1>New Blog</h1>
      <froala :tag="'textarea'" :config="config" v-model="content">
      </froala>
      <button name="save" @click="savePost($event)">Save</button>
      <button name="publish" class="btn btn-primary" @click="savePost($event)">Publish</button>
      <!-- <froalaView v-model="content"></froalaView> -->
    </div>
  </div>
</template>

<script>
const templates = {}
import Vue from 'vue'
import Card from './Card.vue'
templates['Card'] = Card
import BlogPost from './BlogPost.vue'
templates['BlogPost'] = BlogPost
import VideoPost from './VideoPost.vue'
templates['VideoPost'] = VideoPost
import ContentParagraph from './ContentParagraph.vue'
templates['ContentParagraph'] = ContentParagraph
import ParagraphWithPhoto from './ParagraphWithPhoto.vue'
templates['ParagraphWithPhoto'] = ParagraphWithPhoto
import PostHeader from './PostHeader.vue'
templates['PostHeader'] = PostHeader

export default {
  data() {
    return {
      config: {
        events: {
          'froalaEditor.initialized': function (e, editor) {
            console.log('initialized');
          },
          'froalaEditor.commands.after': function (e, editor, cmd, param1, param2) {
            if (cmd === 'tableInsert') {
              const tables = document.querySelectorAll('table')
              tables.forEach(table => {
                table.classList.add('table')
              })
              }
            }
          },
          colorsBackground: ['#F52F22', '#F1428A', '#781DA0', '#343DAC', '#0065AB', '#00B7D6', '#00968B', '#48960C', '#FFDC0B', '#F57600', '#EE4A08', '#80746D', '#737373', '#61717D'],
          tableStyles: {
            'table-compact': 'Compact',
            'table-vertical': 'Vertical'
          },
          heightMin: 500,
          toolbarSticky: false,
          enter: $.FroalaEditor.ENTER_DIV,
          fontFamilySelection: true,
          fontSizeSelection: true,
          paragraphFormatSelection: true,
          toolbarButtons: [
            'fullscreen',
            'bold',
            'italic',
            'underline',
            'strikeThrough',
            'subscript',
            'superscript',
            '|',
            'fontFamily',
            'fontSize',
            'color',
            'inlineStyle',
            'paragraphStyle',
            '|',
            'paragraphFormat',
            'align',
            'formatOL',
            'formatUL',
            'outdent',
            'indent',
            'quote',
            '-',
            'insertLink',
            'insertImage',
            'insertVideo',
            'embedly',
            'insertFile',
            'insertTable',
            '|',
            'emoticons',
            'specialCharacters',
            'insertHR',
            'selectAll',
            'clearFormatting',
            '|',
            'spellChecker',
            'help',
            'html',
            '|',
            'undo',
            'redo',
            'card_dropdown'
          ]
        },
      content: ''
  }
},
  methods: {
    savePost(event) {
      let publish = false
      if (event.target.name === 'publish') {
        publish = true
      }
      const cookie = 'e2fc99db-d6bd-492a-97da-2ef34299ab72'
      document.cookie = {session: cookie}
      const title = document.querySelector('#blog-title').textContent
      const data = {
        title: title,
        body : this.content,
        published: publish
      }
      fetch('http://localhost:8080/blog/new', {
        method: 'POST',
        body: JSON.stringify(data),
        credentials: 'include',
        mode: 'no-cors',
        headers: {
          'content-type': 'application/json'
        }
      })
      .then(res => window.location.href = res.url)
      .catch(err => console.log(err))
    }
  },
  watch: {
    content() {
      console.log(this.content);
    }
  },
  created() {
    console.log('created');
    $.FroalaEditor.DefineIcon('card_dropdown', {NAME: 'file'});
    $.FroalaEditor.RegisterCommand('card_dropdown', {
      title: 'Post Type',
      type: 'dropdown',
      focus: true,
      undo: true,
      refreshAfterCallback: true,
      options: {
        'BlogPost': 'Blog Post',
        'VideoPost': 'Video Post',
        'ContentParagraph': 'Paragraph',
        'ParagraphWithPhoto': 'Paragraph with photo',
        'PostHeader': 'Post Header',
        'Card': 'Card'
      },
      callback: function (cmd, val) {
        const componentCtor = Vue.extend(templates[val])
        let instance
        this.html.insert(`<div class="placeholder"></placeholder>`)
        if (val === 'BlogPost' || val === 'VideoPost' || val === 'PostHeader') {
          const author = prompt('Enter author name')
          instance = new componentCtor({propsData: {author: author}})
        } else {
          instance = new componentCtor()
        }
          instance.$mount('.placeholder')
        },
      refresh: function ($btn) {
        console.log ('do refresh');
      },
      // Callback on dropdown show.
      refreshOnShow: function ($btn, $dropdown) {
        console.log ('do refresh when show');
      }
    })
  }
}
</script>

<style lang="css">
</style>



