<template lang="html">
  <div class="">
    <h2 id="blog-title">Blog Title</h2>
    <div class="byline">
      <img src="../assets/mike-headshot-150.png" alt="" style="height:30px; width:30px; border-radius:100%;">
      <p style="margin:0;">by <a href="">{{ author }} </a>on {{ new Date(Date.now()) }}</p>
    </div>
    <div class="social-links">
      <i class="fa fa-twitter-square social-icon" style="color:#1DA1F2;"></i>
      <i class="fa fa-facebook-square social-icon" style="color:#3B5998;"></i>
      <i class="fa fa-linkedin-square social-icon" style="color:#0077B5;"></i>
      <i class="fa fa-google-plus-square social-icon" style="color:#DB4437;"></i>
    </div>
    <br>
  </div>
</template>

<script>
export default {
  props: ['author']
}
</script>

<style lang="css">
.social-links {
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
}

.byline {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px;
}

.social-icon {
  font-size:40px;
  margin:5px;
}
</style>



